.container {
	max-width: 612px;
	padding-bottom: 75px;
}

.w-35px {
	width: 35px;
}

.mw-550px {
	max-width: 550px;
}

findqo-button {
	height: 40px;
}

.w-80px {
	width: 80px;
}

.w-70px {
	width: 70px;
}

.h-40px {
	height: 40px;
}

.container-fluid {
	z-index: var(--z-index-place-ad-action-btn);
}

.radio-btn-item {
	border: 1px solid var(--general-3);
	height: 40px;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
}

.checkbox-list {
	border: 1px solid var(--general-2);
	height: 40px;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
}

.chip-selected {
	border-radius: 4px !important;
	border: 1px solid #ff5ab8 !important;
	color: #ff5ab8;
	min-height: 40px;
	background-color: #feebf6 !important;
}
